import { setupWorker } from 'msw/browser'
import { setupServer } from 'msw/node'
import { handlers } from './handlers'

// ÊÄßËÉΩÁõëÊéßÈÖçÁΩÆ
interface MockMetrics {
  requestCount: number
  averageResponseTime: number
  errorCount: number
  lastRequestTime: Date | null
}

class MockPerformanceMonitor {
  private metrics: MockMetrics = {
    requestCount: 0,
    averageResponseTime: 0,
    errorCount: 0,
    lastRequestTime: null
  }

  recordRequest(responseTime: number, isError: boolean = false) {
    this.metrics.requestCount++
    this.metrics.lastRequestTime = new Date()
    
    // ËÆ°ÁÆóÂπ≥ÂùáÂìçÂ∫îÊó∂Èó¥
    this.metrics.averageResponseTime = 
      (this.metrics.averageResponseTime * (this.metrics.requestCount - 1) + responseTime) / 
      this.metrics.requestCount

    if (isError) {
      this.metrics.errorCount++
    }
  }

  getMetrics(): MockMetrics {
    return { ...this.metrics }
  }

  reset() {
    this.metrics = {
      requestCount: 0,
      averageResponseTime: 0,
      errorCount: 0,
      lastRequestTime: null
    }
  }
}

export const mockMonitor = new MockPerformanceMonitor()

// ÊµèËßàÂô®ÁéØÂ¢ÉÁöÑMock Worker
export const worker = typeof window !== 'undefined' ? setupWorker(...handlers) : null

// Node.jsÁéØÂ¢ÉÁöÑMock Server (Áî®‰∫éÊµãËØï)
export const server = setupServer(...handlers)

// ÂêØÂä®MockÊúçÂä°
export const startMocking = async () => {
  if (typeof window !== 'undefined') {
    // ÊµèËßàÂô®ÁéØÂ¢É
    if (worker) {
      await worker.start({
        onUnhandledRequest: 'warn',
        serviceWorker: {
          url: '/mockServiceWorker.js'
        }
      })
      
      // Ê∑ªÂä†ËØ∑Ê±ÇÁõëÂê¨Âô®
      worker.events.on('request:start', ({ request }) => {
        const startTime = Date.now()
        request.startTime = startTime
        
        if (process.env.NODE_ENV === 'development') {
          console.log(`üé≠ MSW Request: ${request.method} ${request.url}`)
        }
      })

      worker.events.on('request:match', ({ request }) => {
        const responseTime = Date.now() - (request.startTime || Date.now())
        mockMonitor.recordRequest(responseTime)
        
        if (process.env.NODE_ENV === 'development') {
          console.log(`‚úÖ MSW Matched: ${request.method} ${request.url} (${responseTime}ms)`)
        }
      })

      worker.events.on('request:unhandled', ({ request }) => {
        mockMonitor.recordRequest(0, true)
        
        if (process.env.NODE_ENV === 'development') {
          console.warn(`‚ö†Ô∏è MSW Unhandled: ${request.method} ${request.url}`)
        }
      })

      console.log('üé≠ MSW Mock Worker started with performance monitoring')
    }
  } else {
    // Node.jsÁéØÂ¢É
    server.listen({
      onUnhandledRequest: 'warn'
    })
    console.log('üé≠ MSW Mock Server started')
  }
}

// ÂÅúÊ≠¢MockÊúçÂä°
export const stopMocking = () => {
  if (typeof window !== 'undefined') {
    worker?.stop()
    console.log('üé≠ MSW Mock Worker stopped')
  } else {
    server.close()
    console.log('üé≠ MSW Mock Server stopped')
  }
}

// ÈáçÁΩÆMockÂ§ÑÁêÜÂô®
export const resetMocking = () => {
  if (typeof window !== 'undefined') {
    worker?.resetHandlers()
  } else {
    server.resetHandlers()
  }
  mockMonitor.reset()
}

// Ëé∑ÂèñMockÊÄßËÉΩÊåáÊ†á
export const getMockMetrics = () => mockMonitor.getMetrics()

// ÂºÄÂèëÁéØÂ¢ÉËá™Âä®ÂêØÂä®
if (typeof window !== 'undefined' && process.env.NODE_ENV === 'development') {
  startMocking().catch(console.error)
}

// Á±ªÂûãÊâ©Â±ï
declare global {
  interface Request {
    startTime?: number
  }
} 